@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Overpass:300,400,600,700");
/* base */
*, *::before, *::after {
  text-decoration: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
  font-size: 62.5%;
}

body {
  width: 100%;
  height: 100%;
}

.global-wrapper {
  overflow: hidden;
}

img {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

a {
  display: block;
  text-decoration: none;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}

input, textarea, button {
  -webkit-border-radius: 0;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.clearfix::after {
  content: '';
  display: block;
  clear: both;
}

.dspNone {
  display: none !important;
}

.hidden {
  visibility: hidden;
}

/* reset */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img {
  border: 0;
}

/*
TODO think about hanlding inheritence differently, maybe letting IE6 fail a bit...
*/
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

q:before,
q:after {
  content: '';
}

abbr,
acronym {
  border: 0;
  font-variant: normal;
}

/* to preserve line-height and selector appearance */
sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

/*to enable resizing for IE*/
input,
textarea,
select {
  *font-size: 100%;
}

/*because legend doesn't inherit in IE */
legend {
  color: #000;
}

/* YUI CSS Detection Stamp */
#yui3-css-stamp.cssreset {
  display: none;
}

/* form reset */
legend {
  width: 0px;
  overflow: hidden;
  padding: 0px;
  position: absolute;
}

fieldset,
button,
option,
select,
textarea,
input[type="button"],
input[type="submit"],
input[type="number"],
input[type="email"],
input[type="tel"],
input[type="text"],
input[type="select"],
input[type="option"],
input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  border: none;
  outline: 0;
  margin: 0;
  background: #fff;
}

/* デフォルトのradio、checkboxは非表示 */
input[type=radio],
input[type=checkbox] {
  display: none;
}

/* for firefox */
select {
  text-indent: 0.01px;
  text-overflow: '';
}

/* for ie10 ie11 ie系のプルダウンの矢印を消す ie9は非対応 */
select::-ms-expand {
  display: none;
}

/* --------------------------------
 * body
 * -------------------------------- */
body {
  background-color: #FAFAFA;
  color: #444444;
  font-family: Hiragino Sans, "ヒラギノ角ゴシック", Hiragino Kaku Gothic ProN, "ヒラギノ角ゴ ProN W3", "Avenir", Verdana, YuGothic, "游ゴシック", Roboto, "Droid Sans", Meiryo, "メイリオ", "ＭＳ Ｐゴシック", serif;
}

/* --------------------------------
 * modifier
 * -------------------------------- */
.--pc {
  display: block !important;
}

@media screen and (max-width: 767px) {
  .--pc {
    display: none !important;
  }
}

.--sp {
  display: none !important;
}

@media screen and (max-width: 767px) {
  .--sp {
    display: block !important;
  }
}

.--fixed {
  border-top: 0;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
}

/* --------------------------------
 * layout - common
 * -------------------------------- */
header, footer {
  width: 100%;
}

.home-mv {
  width: 100%;
  margin-bottom: 60px;
}

.breadcrumbs {
  width: 960px;
  margin: 35px auto 15px;
}

.global-wrapper {
  width: 100%;
}

.global-inner {
  width: 960px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 auto;
}

.global-inner main {
  flex: 0 0 640px;
}

.global-inner aside {
  flex: 0 0 280px;
}

.mainSection-wrapper {
  width: 100%;
  background-color: #ffffff;
}

.mainSection-inner {
  padding: 30px;
}

/* --------------------------------
 * Page transition
 * -------------------------------- */
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #EEEEEE;
  /* 背景カラー */
  z-index: 9999;
  /* 一番手前に */
  pointer-events: none;
  /* 他の要素にアクセス可能にするためにポインターイベントは無効に */
  opacity: 0;
  /* 初期値では非表示 */
  transition: opacity .8s ease;
}

body.fadeout::after {
  opacity: 1;
}

/* --------------------------------
 * Top page loading
 * -------------------------------- */
.loading {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0px;
  left: 0px;
  background: #EEEEEE;
  z-index: 9999;
}

.loading-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading-image {
  width: 100px;
  height: 100px;
  background: url("../images/logo01-gold.png") no-repeat;
  background-size: contain;
  margin-bottom: 30px;
  opacity: 0.7;
}

/* アニメーション */
#floatingCirclesG {
  position: relative;
  width: 48px;
  height: 48px;
  margin: auto;
  transform: scale(0.6);
  -o-transform: scale(0.6);
  -ms-transform: scale(0.6);
  -webkit-transform: scale(0.6);
  -moz-transform: scale(0.6);
}

.f_circleG {
  position: absolute;
  background-color: white;
  height: 9px;
  width: 9px;
  border-radius: 4px;
  -o-border-radius: 4px;
  -ms-border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  animation-name: f_fadeG;
  -o-animation-name: f_fadeG;
  -ms-animation-name: f_fadeG;
  -webkit-animation-name: f_fadeG;
  -moz-animation-name: f_fadeG;
  animation-duration: 1.2s;
  -o-animation-duration: 1.2s;
  -ms-animation-duration: 1.2s;
  -webkit-animation-duration: 1.2s;
  -moz-animation-duration: 1.2s;
  animation-iteration-count: infinite;
  -o-animation-iteration-count: infinite;
  -ms-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-direction: normal;
  -o-animation-direction: normal;
  -ms-animation-direction: normal;
  -webkit-animation-direction: normal;
  -moz-animation-direction: normal;
}

#frotateG_01 {
  left: 0;
  top: 19px;
  animation-delay: 0.45s;
  -o-animation-delay: 0.45s;
  -ms-animation-delay: 0.45s;
  -webkit-animation-delay: 0.45s;
  -moz-animation-delay: 0.45s;
}

#frotateG_02 {
  left: 6px;
  top: 6px;
  animation-delay: 0.6s;
  -o-animation-delay: 0.6s;
  -ms-animation-delay: 0.6s;
  -webkit-animation-delay: 0.6s;
  -moz-animation-delay: 0.6s;
}

#frotateG_03 {
  left: 19px;
  top: 0;
  animation-delay: 0.75s;
  -o-animation-delay: 0.75s;
  -ms-animation-delay: 0.75s;
  -webkit-animation-delay: 0.75s;
  -moz-animation-delay: 0.75s;
}

#frotateG_04 {
  right: 6px;
  top: 6px;
  animation-delay: 0.9s;
  -o-animation-delay: 0.9s;
  -ms-animation-delay: 0.9s;
  -webkit-animation-delay: 0.9s;
  -moz-animation-delay: 0.9s;
}

#frotateG_05 {
  right: 0;
  top: 19px;
  animation-delay: 1.05s;
  -o-animation-delay: 1.05s;
  -ms-animation-delay: 1.05s;
  -webkit-animation-delay: 1.05s;
  -moz-animation-delay: 1.05s;
}

#frotateG_06 {
  right: 6px;
  bottom: 6px;
  animation-delay: 1.2s;
  -o-animation-delay: 1.2s;
  -ms-animation-delay: 1.2s;
  -webkit-animation-delay: 1.2s;
  -moz-animation-delay: 1.2s;
}

#frotateG_07 {
  left: 19px;
  bottom: 0;
  animation-delay: 1.35s;
  -o-animation-delay: 1.35s;
  -ms-animation-delay: 1.35s;
  -webkit-animation-delay: 1.35s;
  -moz-animation-delay: 1.35s;
}

#frotateG_08 {
  left: 6px;
  bottom: 6px;
  animation-delay: 1.5s;
  -o-animation-delay: 1.5s;
  -ms-animation-delay: 1.5s;
  -webkit-animation-delay: 1.5s;
  -moz-animation-delay: 1.5s;
}

@keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-o-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-ms-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-webkit-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-moz-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

